.page-regist {
  height: 100%;
  background: url(/system-antd3.0/static/media/bg.10c85f42.jpeg) no-repeat center;
  background-size: cover;
  padding: 50px 0 10px;
  overflow: auto;
}
.page-regist .form-box {
  width: 30%;
  min-width: 340px;
  max-width: 430px;
  margin: 0 auto;
  padding: 40px 30px 10px;
  background-color: rgba(0, 0, 0, 0.02);
  box-shadow: 0 0 20px #ccc;
  border-radius: 4px;
}
.page-regist .form-box .box-header {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 40px;
}
.page-regist .form-box .regist-form .ant-input,
.page-regist .form-box .regist-form .ant-btn {
  font-size: 12px;
}
.page-regist .form-box .regist-form .regist-btn {
  margin-top: 20px;
}
.page-regist .form-box .regist-form .to-login {
  font-size: 12px;
  text-align: center;
}
